  /* 45px */ }

.personal__link--cart {
  position: relative; }

.personal__count {
  position: absolute;
  font-size: 0.625rem;
  /* 10px */
  text-transform: uppercase;
  color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 21px;
  /* 21px */
  height: 21px;
  /* 21px */
  background-color: #fff;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  bottom: -10px;
  /* -10px */
  left: 25px;
  /* 25px */ }

.social__list {
  display: flex;
  align-items: center; }

.social__item {
  width: 38px;
  /* 38px */
  height: 38px;
  /* 38px */
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%; }

.social__item:hover {
  transform: scale(1.2);
  -webkit-transform: scale(1.2);
  -moz-transform: scale(1.2);
  -ms-transform: scale(1.2);
  -o-transform: scale(1.2); }

.social__item + .social__item {
  margin-left: 8px;
  /* 8px */ }

.social__item--footer {
  background-color: transparent; }

.btn {
  font-size: 1.3125rem;
  /* 21px */
  line-height: 1.05em;
  /* 22px */
  font-weight: 800;
  cursor: pointer; }

.btn:hover::before {
  transform: translateX(10px);
  transition: 0.2s ease-in-out;
  -webkit-transition: 0.2s ease-in-out;
  -moz-transition: 0.2s ease-in-out;
  -ms-transition: 0.2s ease-in-out;
  -o-transition: 0.2s ease-in-out;
  -webkit-transform: translateX(10px);
  -moz-transform: translateX(10px);
  -ms-transform: translateX(10px);
  -o-transform: translateX(10px); }

.btn--green {
  background-color: #0aa566; }

.btn--green:hover {
  background-color: #12c37c; }

.btn--border-orange {
  border: 2px solid #f8b231; }

.btn--left {
  transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg); }

._active {
  position: fixed;
  /* overflow-y: hidden; */
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  height: 100vh;
  z-index: 1;
  background-color: #12c37c;
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center; }

@media only screen and (max-width: 1368px) {
  div.header__navigation._active {
    left: 0;
    transition: all 0.2s ease-in-out;
    -webkit-transition: all 0.2s ease-in-out;
    -moz-transition: all 0.2s ease-in-out;
    -ms-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out; }
  ._active + .header__burger-btn .header__burger-line::after {
    transform: rotate(90deg) translate(10px, 0);
    -webkit-transform: rotate(90deg) translate(10px, 0);
    -moz-transform: rotate(90deg) translate(10px, 0);
    -ms-transform: rotate(90deg) translate(10px, 0);
    -o-transform: rotate(90deg) translate(10px, 0); }
  ._active + .header__burger-btn .header__burger-line {
    width: 40px;
    transform: rotate(-45deg) translate(-15px, 0);
    -webkit-transform: rotate(-45deg) translate(-15px, 0);
    -moz-transform: rotate(-45deg) translate(-15px, 0);
    -ms-transform: rotate(-45deg) translate(-15px, 0);
    -o-transform: rotate(-45deg) translate(-15px, 0); }
  ._active + .header__burger-btn .header__burger-line::before {
    transform: scale(0);
    -webkit-transform: scale(0);
    -moz-transform: scale(0);
    -ms-transform: scale(0);
    -o-transform: scale(0); } }

.text {
  font-size: 1.125rem;
  /* 18px */
  line-height: 2em;
  /* 35px */ }

.text--black {
  color: #1e1e1e; }

.text-orange {
  color: #f2612d; }

.title {
  font-size: 3.125rem;
  line-height: 0.8em;
  letter-spacing: -0.02em;
  font-weight: 800; }
  .title--orange {
    color: #f7ae3b; }
  .title--green {
    color: #0aa566; }
  .title--yellow {
    color: #cae941;
    position: relative; }
    .title--yellow::before {
      content: "";
      display: block;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      background-image: url(../images/about/about_line.png);
      top: 60px;
      left: -5px;
      width: 450px;
      height: 13px; }
  @media only screen and (max-width: 360px) {
    .title {
      font-size: 2.75rem;
      line-height: 1.25em;
      font-size: 2.75rem;
      /* 44px */
      line-height: 1.25em; } }

:root {
  font-size: 100%;
  /* 10px */
  /* parallax */
  height: 100%;
  overflow: hidden; }

